	module.exports = {"data":{"markdownRemark":{"html":"<p>Henning ekstraordinære posisjon som kunstner er et resultat av hans allsidighet og lidenskap for musikk. Han opptrer jevnlig med  mange av verdens mest betydningsfulle orkestre og på tilsvarende internasjonale festivaler.</p>\n<p>Henning er en produktiv komponist med mer enn 200 verk på opuslisten.</p>\n<p>Hennings voksende rykte som komponist inkluderer f.eks.«Equinox: 24 Postludier i alle tonearter for fiolin og strykeorkester», som et musikalsk motstykke til en historie av forfatteren Jostein Gaarder, og er blitt hyllet som \"en fascinerende komposisjon å komme tilbake til igjen og igjen\" (MusicWeb International).</p>\n<p>Under Risør Kammermusikkfest 2016, sto Henning for nypremieren av Johan Halvorsens forsvunnete fiolinkonsert  </p>\n<p>Hennings omfattende diskografi omfatter et meget stort antall kritikerroste  innspillinger.  </p>\n<p>Den siste av mange suksesser er  Mozart fiolinkonserter  No. 3, 4 og 5 med Det Norske Kammerorkester, som har mottatt den prestisjetunge  ECHO Award  for Best Concert Recording av disse konsertene.  </p>\n<p>Henning en mottaker av Norges prestisjetunge Grieg-pris og Ole Bull Prisen, og i 2007 ble han tildelt Sibelius-prisen for sine tolkninger og innspilling av Sibelius’ musikk rundt om i verden. Henning er professor ved Barratt Due Musikkinstitutt i Oslo, hvor han også  leder Oslo Camerata.  </p>\n<p>Den norske  fiolinisten Henning Kraggerud er kunstnerisk leder for Nordnorsk Kammerorkester, et engasjement nylig forlenget til 2020. Fra september 2015 er han International Chair i fiolin ved Royal Northern College of Music i Manchester.  </p>\n<p>Henning Kraggerud spiller på en 1744 Guarneri del Gesù, utlånt av Dextra Musica AS. Dette selskapet er grunnlagt av Sparebankstiftelsen DNB.</p>\n<p>Foto: Kaupo Kikkas.</p>","timeToRead":1,"excerpt":"Henning ekstraordinære posisjon som kunstner er et resultat av hans allsidighet og lidenskap for musikk. Han opptrer jevnlig med  mange av…","id":"/Users/thomasheimstad/Sites/Obo/src/posts/artister/henning-kraggerud.md absPath of file >>> MarkdownRemark","frontmatter":{"workname":null,"composedin":null,"startDate":null,"startTime":null,"location":null,"artists":null,"medialink":"https://www.youtube.com/embed/gNcFQP78bhA","title":"Henning Kraggerud","dedicatedTo":null,"author":null,"date":"2019-06-25T12:28:12.048Z","category":"artister","tags":["fiolinist"],"thumbnail":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.4991055456171736,"src":"/static/HenningKraggerud-9f0a9f87f66f248f86a02f4ecb44d7cc-9d071.jpg","srcSet":"/static/HenningKraggerud-9f0a9f87f66f248f86a02f4ecb44d7cc-309dd.jpg 480w,\n/static/HenningKraggerud-9f0a9f87f66f248f86a02f4ecb44d7cc-9d071.jpg 838w","sizes":"(max-width: 838px) 100vw, 838px"}}}},"fields":{"slug":"/artister/henning-kraggerud/","artist":null,"opuses":[{"frontmatter":{"title":"8","category":"opus","workname":"Fiolinsonate nr. 1 i F-dur","thumbnail":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1,"src":"/static/opus8-b4b2877bc5069735fd199df0f8cc05c9-466fa.jpg","srcSet":"/static/opus8-b4b2877bc5069735fd199df0f8cc05c9-27b23.jpg 90w,\n/static/opus8-b4b2877bc5069735fd199df0f8cc05c9-0afbd.jpg 180w,\n/static/opus8-b4b2877bc5069735fd199df0f8cc05c9-466fa.jpg 360w,\n/static/opus8-b4b2877bc5069735fd199df0f8cc05c9-43088.jpg 540w,\n/static/opus8-b4b2877bc5069735fd199df0f8cc05c9-545bd.jpg 720w,\n/static/opus8-b4b2877bc5069735fd199df0f8cc05c9-8c160.jpg 765w","sizes":"(max-width: 360px) 100vw, 360px"}}}},"fields":{"slug":"/opus8/","artist":[{"frontmatter":{"title":"Henning Kraggerud","tags":["fiolinist"]}},{"frontmatter":{"title":"Leif Ove Andsnes","tags":["pianist"]}}]}}]}}},"pathContext":{"slug":"/artister/henning-kraggerud/"}}

